	module.exports = {"data":{"markdownRemark":{"html":"<p>This episode, I talk with Zsolt Olah, one of my ID heroes, about two different projects that illustrate how context is king. Zsolt shares how, in two separate situations, the thing that he was asked for, a course, turned out to not be the thing that was needed. He discusses his design process and how we, as newbies, can learn to ask the right questions about context that will guide our clients to the real problem and more effective solutions.</p>\n<iframe src=\"https://simplecast.com/e/59990?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>Links to more about Zsolt on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"May 04, 2017","path":"/blog/dearid-episode-43-a-tale-of-two-projects-with-zsolt-olah","title":"#DearID Episode 43: A Tale of Two Projects with Zsolt Olah","tags":["dear id","instructional design","newbie"],"categories":["Dear ID Show","Design"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

